import { Component, ViewChild } from '@angular/core';
import { MenuController, NavController, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomePage } from '../pages/home/home';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild('content') _NAV : NavController
  rootPage:any = HomePage;

  public people : Array<{ name: string, icon: string, link: any}> = [];
  public profitcenters : Array<{ name: string, icon: string, link: any}> = [];
  public regions : Array<{ name: string, icon: string, link: any}> = [];

  constructor(platform: Platform, 
              statusBar: StatusBar, 
              splashScreen: SplashScreen,
              private _MENU: MenuController) 
              {
                this.people = [
                  { 
                    name: 'Region Managers',
                    icon: 'people',
                    link: 'RegionManagerPage'
                  },
                  {
                    name: 'Region Support',
                    icon: 'people',
                    link: 'RegionSupportPage'
                  },
                  {
                    name: 'National Support Center',
                    icon: 'people',
                    link: 'NationalSupportPage'
                  }
                ];

                this.profitcenters = [
                  {
                    name: 'Profit Centers',
                    icon: 'pulse',
                    link: 'ProfitcenterPage'
                  },
                  {
                    name: 'Showrooms',
                    icon: 'easel',
                    link: 'ShowroomPage'
                  }
                ];

                this.regions = [
                  {
                    name: 'Region Map',
                    icon: 'map',
                    link: 'RegionPage'
                  }
                ]
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
    });
  }

  sectionToLoad(page){
    let link : string = page.link,
    section: string = page.name;
    this._NAV.setRoot(link, section);
    this._MENU.close();
  }

  goHome(){
    this._NAV.setRoot(HomePage);
    this._MENU.close();
  }
}




